import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";

// Component
import Loader from "../components/Loader";
import Message from "../components/Message";

import { getUserDetails, updateUserProfile } from "../actions/userActions";
import { USER_UPDATE_PROFILE_RESET } from "../constants/userConstants";
import { listMyOrders } from "../actions/orderActions";

const ProfilePage = () => {
	const [first_name, setFirst_name] = useState("");
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [confirmPassword, setConfirmPassword] = useState("");
	const [message, setMessage] = useState({ text: "", variant: "" });

	const navigate = useNavigate();
	const dispatch = useDispatch();

	const userDetails = useSelector((state) => state.userDetails);
	const { loading, error, user } = userDetails;

	const userLogin = useSelector((state) => state.userLogin);
	const { userInfo } = userLogin;
	// console.log("userInfo:",userInfo);

	const userUpdateProfile = useSelector((state) => state.userUpdateProfile);
	const { success } = userUpdateProfile; // userUpdateProfilesuccess action 

	const orderListMy = useSelector((state) => state.orderListMy);
	const { loading: loadingOrders, error: errorOrders, orders } = orderListMy;

	// console.log(orders);

	useEffect(() => {
		if (!userInfo) {
			// 
			navigate("/login");
		} else {
			if (!user || !user.first_name || success || userInfo.id !== user.id) { //  id(userInfo.id)  id(user.id) 
				dispatch({ type: USER_UPDATE_PROFILE_RESET }); //  userUpdateProfile 
				dispatch(getUserDetails("profile"));
				dispatch(listMyOrders());
			} else {
				setFirst_name(user.first_name);
				setEmail(user.email);
			}
		}
	}, [dispatch, navigate, userInfo, user, success]);

	const submitHandler = (e) => {
		e.preventDefault();

		if (password !== confirmPassword) {
			setMessage({ text: "", variant: "danger" });
		} else {
			// console.log("...");
			dispatch(
				updateUserProfile({
					id: user.id,
					first_name: first_name,
					email: email,
					password: password,
				})
			); //  id,first_name,email,password  action user 
			setMessage({ text: "", variant: "success" });
		}
	};

	// useEffect(() => {
	//   console.log('nothing')
	//   if (user && Object.keys(user).length > 0) {
	//     //  user 
	//     console.log(user); //  user 
	//   }
	// }, [user]);

	return (
		<Row>
			<Col md={2}>
				<h2 style={{ marginBottom: "20px"}}></h2>
				{/* {message && <Message variant={message.variant}>{message.text}</Message>} */}
				{message?.text && <Message variant={message.variant}>{message.text}</Message>}
				{/*  */}
				{error && <Message variant='danger'>{error}</Message>}
				{loading && <Loader />} {/* loading  action  <Loadind />  Component */}
				<Form onSubmit={submitHandler}>
					<Form.Group controlId='first_name' className='m-3'>
						<Form.Label>ID</Form.Label>
						<Form.Control required type='name' placeholder='ID' value={first_name} onChange={(e) => setFirst_name(e.target.value)}></Form.Control>
					</Form.Group>

					<Form.Group controlId='email' className='m-3'>
						<Form.Label></Form.Label>
						<Form.Control required type='email' placeholder='' value={email} onChange={(e) => setEmail(e.target.value)} autoComplete='username'></Form.Control>
					</Form.Group>

					<Form.Group controlId='password' className='m-3'>
						<Form.Label></Form.Label>
						<Form.Control type='password' placeholder='' value={password} onChange={(e) => setPassword(e.target.value)}></Form.Control>
					</Form.Group>

					<Form.Group controlId='passwordConfirm' className='m-3'>
						<Form.Label></Form.Label>
						<Form.Control type='password' placeholder='' value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} autoComplete='new-password'></Form.Control>
					</Form.Group>

					<Button type='submit' variant='primary' className='m-3'>
						
					</Button>
				</Form>
			</Col>

			<Col md={10}>
				<h2 style={{ marginBottom: "20px"}}></h2>
				{loadingOrders ? (
					<Loader />
				) : errorOrders ? (
					<Message variant='danger'>{errorOrders}</Message>
				) : (
					<Table striped hover responsive className='table-sm table-light'>
						{/*  */}
						<thead className='table-dark'>
							<tr>
								<th>ID</th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						{/*  */}
						<tbody>
							{orders.map((order) => (
								<tr key={order.id}>
									<td>{order.id}</td>
									<td>{order.createdAt.substring(0, 10)}</td>
									<td>${Math.round(order.totalPrice)}</td>
									<td>{order.isPaid ? order.paidAt.substring(0, 10) : <i className='fas fa-times' style={{ color: "red" }}></i>}</td>
									<td>
										<LinkContainer to={`/order/${order.id}`}>
											<Button className='btn-sm' variant='dark'>
												
											</Button>
										</LinkContainer>
									</td>
								</tr>
							))}
						</tbody>
					</Table>
				)}
			</Col>
		</Row>
	);
};

export default ProfilePage;
