import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Form, Button, Row, Col } from "react-bootstrap";
// Component
import Loader from "../components/Loader";
import Message from "../components/Message";
import FormContainer from "../components/FormContainer";
import { login } from "../actions/userActions";

const LoginPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const redirect = location.search ? location.search.split("=")[1] : "/"; // redirectredirectredirect '/'

  const userLogin = useSelector((state) => state.userLogin); // storeuserLogin
  const { error, loading, userInfo } = userLogin; //  loading  userActions  login  loading

  const submitHandler = (e) => {
    e.preventDefault();
    console.log("submitHandler");
    dispatch(login(email, password));
  };

  useEffect(() => {
    if (userInfo) {
      //  redirect
      navigate(redirect);
    }
  }, [userInfo, redirect, navigate]); // userInfo,redirect,navigate useEffect

  return (
    <FormContainer>
      <h1></h1>
      {error && <Message variant="danger">{error}</Message>}
      {/*  backend  user_view  MytokenObtainPairView  Default  */}
      {/* loading  action  <Loadind />  Component */}
      {loading && <Loader />}
      <Form onSubmit={submitHandler}>
        <Form.Group controlId="email" className="my-3">
          <Form.Label></Form.Label>
          <Form.Control
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="password" className="my-3">
          <Form.Label></Form.Label>
          <Form.Control
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Button type="submit" variant="primary" className="my-3 w-100">
          
        </Button>
      </Form>

      <Row className="py-3 text-center">
        <Col>
           ?{" "}
          <Link to={redirect ? `/register?redirect=${redirect}` : "/register"}>
            
          </Link>
        </Col>
      </Row>
    </FormContainer>
  );
};

export default LoginPage;
