import React, { useState, useEffect } from "react";
import { Row, Col, ListGroup, Image, Card, Table, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { ORDER_PAY_RESET } from "../constants/orderConstants";
import { ORDER_DELIVER_RESET } from "../constants/orderConstants";
// actions
import { getOrderDetails, payOrder, deliverOrder } from "../actions/orderActions";
// components
// import CheckoutSteps from "../components/CheckoutSteps";
import Message from "../components/Message";
import Loader from "../components/Loader";
import PayPalPayment from "../components/PayPalPayment";

const OrderPage = () => {
	const navigate = useNavigate();
	const dispatch = useDispatch();
	const { orderId } = useParams(); // useParams()  {} 

	const [sdkReady, setSdkReady] = useState(false);

	const orderDetails = useSelector((state) => state.orderDetails);
	const { order, error, loading } = orderDetails;
	// console.log(order);

	const orderPay = useSelector((state) => state.orderPay);
	const { loading: loadingPay, success: successPay } = orderPay; //  loadingsuccess  loading 

	const orderDeliver = useSelector((state) => state.orderDeliver);
	const { loading: loadingDeliver, success: successDeliver } = orderDeliver;

	// const userLogin = useSelector((state) => state.userLogin);
	// const { userInfo } = userLogin;
	// 
	const { userInfo } = useSelector((state) => state.userLogin);

	if (!loading && !error) {
		//  order  cannot read property of undefined
		order.itemsPrice = order.orderItems.reduce((acc, currentItem) => acc + currentItem.price * currentItem.qty, 0); // reduce()  callback functionacc  item 
	}

	// const addPayPalScript = async () => {
	//   // const { data: clientId } = await axios.get("/api/config/paypal"); //  axios  index.html  axios CDN
	//   // console.log(clientId);
	//   const script = document.createElement("script");
	//   script.type = "text/javascript";
	//   script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}&currency=USD`;
	//   script.async = true;
	//   script.onload = () => {
	//     setSdkReady(true);
	//   };
	//   document.body.appendChild(script);
	// };

	useEffect(() => {
		if (!order || successPay || order.id !== Number(orderId) || successDeliver) {
			dispatch({ type: ORDER_PAY_RESET }); //  RESET  <Loader />
			dispatch({ type: ORDER_DELIVER_RESET });

			dispatch(getOrderDetails(orderId));
		} else if (!order.isPaid) {
			setSdkReady(true);
		}
	}, [dispatch, navigate, order, orderId, successPay, successDeliver]);

	const successPaymenyHadler = (paymentResult) => {
		console.log(paymentResult);
		dispatch(payOrder(orderId, paymentResult));
	};

	const deliverOrderHandler = () => {
		dispatch(deliverOrder(order));
	};

	return (
		<>
			{loading ? (
				<Loader />
			) : error ? (
				<Message variant='danger'>{error}</Message>
			) : (
				<Row>
					<h2>{order ? order.id : ""}</h2>
					<Col md={8}>
						<ListGroup variant='flush'>
							<ListGroup.Item>
								<h2></h2>
								<p>
									<strong></strong>
									{order.user.first_name} {order.user.last_name}
								</p>
								<p>
									<strong>Email: </strong>
									{/*  Email  LinkContainer */}
									<a href={`mailto:${order.user.email}`}>{order.user.email}</a>
								</p>
								<p>
									<strong></strong>
									{order.shippingAddress.address}, {order.shippingAddress.city}
									{"  "}
									{order.shippingAddress.postalCode},{"  "}
									{order.shippingAddress.country}
								</p>
								{order.isDelivered ? <Message variant='success'> {order.deliveredAt}</Message> : <Message variant='danger'></Message>}
							</ListGroup.Item>

							<ListGroup.Item>
								<h2></h2>
								<p>
									<strong></strong>
									{order.paymentMethod}
								</p>
								{order.isPaid ? <Message variant='success'> {order.paidAt}</Message> : <Message variant='danger'></Message>}
							</ListGroup.Item>

							<ListGroup.Item>
								<h2></h2>
								{order.orderItems.length === 0 ? (
									<Message></Message>
								) : (
									<Table striped hover responsive className='table-sm'>
										<thead>
											<tr>
												<th></th>
												<th></th>
												<th></th>
												<th></th>
												<th></th>
											</tr>
										</thead>
										<tbody>
											{order.orderItems.map((item, index) => (
												<tr key={index}>
													<td>
														<Image src={item.image} alt={item.name} fluid rounded style={{ width: "70px", height: "50px" }} />
													</td>
													<td style={{ verticalAlign: "middle" }}>{item.name}</td>
													<td style={{ verticalAlign: "middle" }}>{item.qty}</td>
													<td style={{ verticalAlign: "middle" }}>{item.price}</td>
													<td style={{ verticalAlign: "middle" }}>{item.qty * item.price}</td>
												</tr>
											))}
										</tbody>
									</Table>
								)}
							</ListGroup.Item>
						</ListGroup>
					</Col>

					<Col md={4}>
						<Card>
							<ListGroup variant='flush'>
								<ListGroup.Item>
									<h2></h2>
								</ListGroup.Item>

								<ListGroup.Item>
									<Row>
										<Col></Col>
										<Col>${order.itemsPrice}</Col>
									</Row>
								</ListGroup.Item>

								<ListGroup.Item>
									<Row>
										<Col></Col>
										<Col>${order.shippingPrice}</Col>
									</Row>
								</ListGroup.Item>

								<ListGroup.Item>
									<Row>
										<Col></Col>
										<Col>${order.totalPrice}</Col>
									</Row>
								</ListGroup.Item>

								{/*   */}
								{loadingDeliver && <Loader />}
								{userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered && (
									<ListGroup.Item>
										<Button type='button' className='btn btn-block' onClick={deliverOrderHandler}>
											
										</Button>
									</ListGroup.Item>
								)}

								{/* PayPal  */}
								{!order.isPaid && (
									<ListGroup.Item>
										{" "}
										{loadingPay && <Loader />}
										{!sdkReady ? <Loader /> : <PayPalPayment amount={order.totalPrice} onSuccess={successPaymenyHadler} />}
									</ListGroup.Item>
								)}
							</ListGroup>
						</Card>
					</Col>
				</Row>
			)}
		</>
	);
};

export default OrderPage;
