// import { configureStore, combineReducers } from "@reduxjs/toolkit";
import { configureStore } from "@reduxjs/toolkit";
import thunk from "redux-thunk";
// reducers
import { productListReducer, productDetailReducer, productDeleteReducer, productCreateReducer, productUpdateReducer, productCreateReviewReducer, productTopRatedReducer } from "./reducers/ProductReducers";
import { cartReducer } from "./reducers/cartReducers";
import {
	userLoginReducer,
	userRegisterReducer,
	userDetailsReducer,
	userUpdateProfileReducer,
	// Admin 
	userListReducer,
	userDeleteReducer,
	userUpdateReducer,
} from "./reducers/userReducers";
import { orderCreateReducer, orderDetailsReducer, orderPayReducer, orderListMyReducer, orderListAllReducer, orderDeliverReducer } from "./reducers/orderReducers";
// const reducer = combineReducers()

const cartItemsFromStorage = localStorage.getItem("") ? JSON.parse(localStorage.getItem("")) : []; //  ActionCreator localStorage.setItem('X',JSON.stringify(cartItems)) 

const userInfoFromStorage = localStorage.getItem("userInfo") ? JSON.parse(localStorage.getItem("userInfo")) : null;

const shippingAddressFromStorage = localStorage.getItem("shippingAddress") ? JSON.parse(localStorage.getItem("shippingAddress")) : {};

const paymentMethodFromStorage = localStorage.getItem("paymentMethod") ? JSON.parse(localStorage.getItem("paymentMethod")) : {};

//  State
export const initialState = {
	cart: {
		cartItems: cartItemsFromStorage,
		shippingAddress: shippingAddressFromStorage,
		paymentMethod: paymentMethodFromStorage,
	},
	userLogin: { userInfo: userInfoFromStorage },
	// orderListAll: { orders: [] },
	// orderDetails: { order: {} },
	// orderPay: { success: false },
	// orderListMy: { orders: [] },
	// orderCreate: { success: false },
	// orderDeliver: { success: false },
	// userList: { users: [] },
	// userDelete: { success: false },
	// userUpdate: { success: false },
	// productList: { products: [] },
	// productDetail: { product: {} },
	// productDelete: { success: false },
	// productCreate: { success: false },
	// productUpdate: { success: false },
};
//  action creator
const middleware = [thunk]; //  applyMiddleware

const store = configureStore({
	reducer: {
		// statecomponentuseSelector
		// 
		productList: productListReducer,
		productDetail: productDetailReducer,
		productDelete: productDeleteReducer,
		productCreate: productCreateReducer,
		productUpdate: productUpdateReducer,
		productCreateReview: productCreateReviewReducer,
		productTopRated: productTopRatedReducer,
		// 
		cart: cartReducer,
		// 
		userLogin: userLoginReducer,
		userRegister: userRegisterReducer,
		userDetails: userDetailsReducer,
		userUpdateProfile: userUpdateProfileReducer,
		userList: userListReducer,
		userDelete: userDeleteReducer,
		userUpdate: userUpdateReducer,
		// 
		orderCreate: orderCreateReducer,
		orderDetails: orderDetailsReducer,
		orderPay: orderPayReducer,
		orderListMy: orderListMyReducer,
		orderListAll: orderListAllReducer,
		orderDeliver: orderDeliverReducer,
	},
	preloadedState: initialState,
	middleware: middleware,
});

// createStore(reducer, [preloadedState], [enhancer]); //  deprecated
// configureStore({reducer, preloadedState, middleware, enhancers}) // 

export default store;
