// Viewsaction

import axios from "axios";
import {
  CART_ADD_ITEM,
  CART_REMOVE_ITEM,
  CART_SAVE_SHIPPING_ADDRESS_ITEM,
  CART_SAVE_PAYMENT_METHOD_ITEM,
} from "../constants/cartsConstants";

// action creator 
export const addToCart = (id, qty) => async (dispatch, getState) => {
  // getStatestore.jspull
  const { data } = await axios.get(`/api/products/${id}`);
  dispatch({
    type: CART_ADD_ITEM,
    payload: {
      product_id: data.id,
      name: data.name,
      desc: data.description,
      image: data.image,
      price: data.price,
      countInStock: data.countInStock,
      qty,
    },
  });
  localStorage.setItem(
    "",
    JSON.stringify(getState().cart.cartItems)
  );
};

export const removeFromCart = (id) => (dispatch, getState) => {
  // getState  store 
  dispatch({
    type: CART_REMOVE_ITEM,
    payload: id,
  });
  localStorage.setItem(
    "",
    JSON.stringify(getState().cart.cartItems)
  );
};

export const saveShippingAddress = (data) => (dispatch) => {
  // const { name, email, address, city, postalCode, country } = data;

  dispatch({
    type: CART_SAVE_SHIPPING_ADDRESS_ITEM,
    payload: data,
  });

  localStorage.setItem("shippingAddress", JSON.stringify(data));
};

export const savePaymentMethod = (data) => (dispatch) => {
  dispatch({
    type: CART_SAVE_PAYMENT_METHOD_ITEM,
    payload: data,
  });

  localStorage.setItem("paymentMethod", JSON.stringify(data));
};
