import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, useParams, useNavigate } from "react-router-dom";
import { Row, Col, Image, ListGroup, Button, Form, Card } from "react-bootstrap";
// Component
import Rate from "../components/Rate";
import Loader from "../components/Loader";
import Message from "../components/Message";
// Action
import { listProductDetail, createProductReview } from "../actions/productActions";
// Constant
import { PRODUCT_CREATE_REVIEW_RESET } from "../constants/productsConstants";

// 
const ProductPage = () => {
	const { id } = useParams(); // V6  App.js  :id App.js Route /product/:id 
	const navigate = useNavigate();
	const dispatch = useDispatch();

	const [qty, setQty] = useState(1);
	const [rating, setRating] = useState(1);
	const [comment, setComment] = useState("");

	const productDetail = useSelector((state) => state.productDetail);
	const { error, loading, product } = productDetail; // store.jsreducer

	const { userInfo } = useSelector((state) => state.userLogin);

	const productCreateReview = useSelector((state) => state.productCreateReview);
	const { loading: loadingCreateReview, error: errorCreateReview, success: successCreateReview } = productCreateReview;

	useEffect(() => {
		if (successCreateReview) {
			setRating(1);
			setComment("");
			dispatch({ type: PRODUCT_CREATE_REVIEW_RESET });
		}
		dispatch(listProductDetail(id));

		// async function fetchProduct() {
		//   // django urls.pyurls
		//   const { data } = await axios.get(`/api/products/${id}`); // url / djangourlreacturl http://localhost:3000/product/api/products/3

		//   setProduct(data);
		// }
		// fetchProduct();
	}, [dispatch, id, successCreateReview]);

	const addToCartHandler = () => {
		navigate(`/cart/${id}?qty=${qty}`);
		// console.log("",id)
	};

	const submitReviewHandler = (e) => {
		e.preventDefault();
		dispatch(createProductReview(product.id, { rating, comment }));
	};

	return (
		<div>
			<Link to='/' className='btn btn-light my-3'>
				
			</Link>
			{loading ? (
				<h1>
					<Loader />
				</h1>
			) : error ? (
				<h3>
					<Message variant='danger'>{error}</Message>
				</h3>
			) : (
				<>
					<Card className='p-3 shadow-sm rounded'>
						<Row>
							<Col md={4}>
								<Image src={product.image} alt={product.name} fluid className='rounded'></Image>
							</Col>
							<Col md={8}>
								{/* flush =  border */}
								<ListGroup variant='flush'>
									<ListGroup.Item>
										<h3>{product.name}</h3>
									</ListGroup.Item>

									<ListGroup.Item>
										<h4>{product.description}</h4>
									</ListGroup.Item>

									<ListGroup.Item>
										<Rate value={product.rating} text={`${product.numReviews} `} color={`#f8e825`} />
									</ListGroup.Item>

									<ListGroup.Item>
										<h3>${product.price}</h3>
									</ListGroup.Item>

									<ListGroup.Item>
										<Col>{product.countInStock > 0 ? "" : "QQ"}</Col>
									</ListGroup.Item>

									{product.countInStock > 0 && (
										<ListGroup.Item>
											<Row className='align-items-center'>
												<Col md={2}></Col>
												<Col xs='auto'>
													<Form.Control as='select' value={qty} className='form-select' onChange={(e) => setQty(e.target.value)}>
														{
															// [0,1,2,...]
															[...Array(product.countInStock).keys()].map((x) => (
																<option key={x + 1} value={x + 1}>
																	{x + 1}
																</option>
															))
														}
													</Form.Control>
												</Col>
											</Row>
										</ListGroup.Item>
									)}

									<ListGroup.Item>
										<Button onClick={addToCartHandler} className='w-100 btn-block' disabled={product.countInStock === 0} type='button'>
											
										</Button>
									</ListGroup.Item>
								</ListGroup>
							</Col>
						</Row>
					</Card>

					<Row className='my-3'>
						<Col md={6}>
							<h2></h2>
							<ListGroup>
								{product.reviews.length === 0 && <Message variant='info'></Message>}
								{product.reviews.map((review) => (
									<ListGroup.Item key={review.id}>
										<div className='my-2 text-bold'>{review.name}</div>
										<div className='my-2'>
											<Rate value={review.rating} color={`#f8e825`} />
										</div>
										<div className='my-2'>{review.createdAt.substring(0, 10)}</div>
										<div className='my-2'>{review.comment}</div>
									</ListGroup.Item>
								))}

								<ListGroup.Item>
									<h2 className='my-3'></h2>
									{loadingCreateReview && <Loader />}
									{errorCreateReview && <Message variant='danger'>{errorCreateReview}</Message>}
									{successCreateReview && <Message variant='success'></Message>}
									{userInfo ? (
										<Form onSubmit={submitReviewHandler}>
											<Form.Group controlId='rating' className='my-2'>
												<Form.Label></Form.Label>
												<Form.Control as='select' value={rating} onChange={(e) => setRating(e.target.value)}>
													<option value=''></option>
													<option value='1'>1 - </option>
													<option value='2'>2 - </option>
													<option value='3'>3 - </option>
													<option value='4'>4 - </option>
													<option value='5'>5 - </option>
												</Form.Control>
											</Form.Group>

											<Form.Group controlId='comment'>
												<Form.Label></Form.Label>
												<Form.Control as='textarea' rows={5} value={comment} onChange={(e) => setComment(e.target.value)} placeholder='...' />
											</Form.Group>

											<Button type='submit' disabled={loadingCreateReview} variant='primary' className='my-2'>
												
											</Button>
										</Form>
									) : (
										<Message variant='info'>
											 <Link to='/login'></Link> 
										</Message>
									)}
								</ListGroup.Item>
							</ListGroup>
						</Col>
					</Row>

				</>
			)}
		</div>
	);
};

export default ProductPage;
