import { 
    CART_ADD_ITEM, 
    CART_REMOVE_ITEM,
    CART_SAVE_SHIPPING_ADDRESS_ITEM,
    CART_SAVE_PAYMENT_METHOD_ITEM,
    CART_CLEAR_ITEMS,
} from "../constants/cartsConstants";

export const cartReducer = (state = { cartItems: [],shippingAddress:{} }, action) => {
    switch (action.type) {
        case CART_ADD_ITEM: 
            //  product  qty 
            const item = action.payload;
            //  action.payload  cartItems 
            //  x item product_id product_id  addToCart action creator  payload 
            const existItem = state.cartItems.find(x => x.product_id === item.product_id)  // object
            if(existItem){
                return {
                    ...state, //  pruduct item
                    cartItems:state.cartItems.map( x=>x.product_id===existItem.product_id ? item : x) 
                }
            }else{
                return {
                    // original state
                    ...state, // productupdate qtyoriginal dataitemcartItems
                    cartItems:[...state.cartItems,item]
                }
            }
        case CART_REMOVE_ITEM:
            return {
                ...state,
                //  id  action.payload  action.payload  id
                //  UPDATE state.cartItems
                cartItems:state.cartItems.filter(x=> x.product_id !== action.payload)
            }
        case CART_SAVE_SHIPPING_ADDRESS_ITEM:
            return {
                ...state,
                shippingAddress:action.payload
            }
        case CART_SAVE_PAYMENT_METHOD_ITEM:
            return {
                ...state,
                paymentMethod: action.payload
            }
        case CART_CLEAR_ITEMS:
            return {
                ...state,
                cartItems:[]
            } // 
        default:
            return state;
    }
};