import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { Form, Button, Row, Col } from "react-bootstrap";
// Component
import Loader from "../components/Loader";
import Message from "../components/Message";
import FormContainer from "../components/FormContainer";

import { register } from "../actions/userActions";

const RegisterPage = () => {
  const [first_name, setFirst_name] = useState('')
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState("");

  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const redirect = location.search ? location.search.split("=")[1] : "/"; // redirectredirectredirect '/'

  const userRegister = useSelector((state) => state.userRegister); // storeuserLogin
  const { error, loading, userInfo } = userRegister; // userLoginerror,Loader,userInfo

  const submitHandler = (e) => {
    e.preventDefault();
    
    if (password !== confirmPassword) {
      setMessage("");
    } else {
      dispatch(register(first_name, email, password));
    }
  };

  useEffect(() => {
    if (userInfo) {
      //  redirect
      navigate(redirect);
    }
  }, [userInfo, redirect, navigate]); // userInfo,redirect,navigate useEffect

  return (
    <FormContainer>
      <h1></h1>
      {message && <Message variant="danger">{message}</Message>}
      {error && <Message variant="danger">{error}</Message>}
      {/* loading  action  <Loadind />  Component */}
      {loading && <Loader />}

      <Form onSubmit={submitHandler}>
        <Form.Group controlId="first_name">
          <Form.Label>ID</Form.Label>
          <Form.Control
            required
            type="name"
            placeholder="ID"
            value={first_name}
            onChange={(e) => setFirst_name(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="email">
          <Form.Label></Form.Label>
          <Form.Control
            required
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            autoComplete="username"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="password">
          <Form.Label></Form.Label>
          <Form.Control
            required
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            autoComplete="current-password"
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="passwordConfirm">
          <Form.Label></Form.Label>
          <Form.Control
            required
            type="password"
            placeholder=""
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            autoComplete="new-password"
          ></Form.Control>
        </Form.Group>

        <Button type="submit" variant="primary" className="mt-3">
          
        </Button>
      </Form>

      <Row className="py-3">
        <Col>
          ?{" "}
          <Link to={redirect ? `/login?redirect=${redirect}` : "/login"}>
            
          </Link>
        </Col>
      </Row>
    </FormContainer>
  );
};

export default RegisterPage;
