import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams, useNavigate } from "react-router-dom";
import { Form, Button } from "react-bootstrap";
import axios from "axios";
// Component
import Loader from "../components/Loader";
import Message from "../components/Message";
import FormContainer from "../components/FormContainer";

import { listProductDetail } from "../actions/productActions";
import { updateProduct } from "../actions/productActions";
import { PRODUCT_UPDATE_RESET } from "../constants/productsConstants";

const ProductEditPage = () => {
	const navigate = useNavigate();
	const { productID } = useParams(); //  App.js  :productID 

	const [name, setName] = useState("");
	const [price, setPrice] = useState(0);
	const [image, setImage] = useState("");
	const [brand, setBrand] = useState("");
	const [category, setCategory] = useState("");
	const [countInStock, setCountInStock] = useState(0);
	const [description, setDescription] = useState("");
	const [ImageUploading, setImageUploading] = useState("");

	const dispatch = useDispatch(); //  action  hook

	const productDetail = useSelector((state) => state.productDetail); // 
	const { loading, error, product } = productDetail;

	const productUpdate = useSelector((state) => state.productUpdate);
	const { loading: loadingUpdate, error: errorUpdate, success: successUpdate } = productUpdate;

	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(
			updateProduct({
				id: productID,
				name,
				price,
				image,
				brand,
				category,
				countInStock,
				description,
			})
		);
	};

	useEffect(() => {
		if (successUpdate) {
			dispatch({ type: PRODUCT_UPDATE_RESET });
			navigate("/admin/productlist");
		} else {
			if (!product || !product.name || product.id !== Number(productID)) {
				dispatch(listProductDetail(productID));
			} else {
				setName(product.name);
				setPrice(product.price);
				setImage(product.image);
				setBrand(product.brand);
				setCategory(product.category);
				setCountInStock(product.countInStock);
				setDescription(product.description);
			}
		}
	}, [dispatch, product, productID, navigate, successUpdate]);

	const uploadImageHandler = async (e) => {
		// console.log(e.target.value);
		const file = e.target.files[0];

		const formData = new FormData();
		formData.append("product_image", file); // product_image  uploadProductImage view 
		formData.append("product_id", productID);

		try {
			// const config = {
			// 	headers: { "Content-Type": "multipart/form-data" },
			// }
			const { data } = await axios.post("/api/products/upload/", formData); //  uploadProductImage view
			console.log(data);
			setImage(data); //  return Response 
			setImageUploading(false); // 
		} catch (error) {
			setImageUploading(false);
		}
	};

	return (
		<>
			<Link to='/admin/productlist' className='btn btn-light my-3'>
				
			</Link>

			<h1></h1>

			{/*  loading  error */}
			{loadingUpdate && <Loader />}
			{errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}

			{loading ? (
				<Loader />
			) : error ? (
				<Message variant='danger'>{error}</Message>
			) : (
				<FormContainer>
					<Form onSubmit={submitHandler}>
						<Form.Group controlId='name' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='name' placeholder='ID' value={name} onChange={(e) => setName(e.target.value)}></Form.Control>
						</Form.Group>

						<Form.Group controlId='price' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='number' placeholder='' value={price} onChange={(e) => setPrice(e.target.value)}></Form.Control>
						</Form.Group>

						{/* Form.File  5.x  deprecated  */}
						{/* <Form.File id='image-file' label='' custom onChange={uploadImageHandler}></Form.File> */}
						<Form.Group controlId='image' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='text' placeholder='' value={image} onChange={(e) => setImage(e.target.value)}></Form.Control>
							<Form.Control type='file' onChange={uploadImageHandler} />
						</Form.Group>
						{ImageUploading && <Loader />}

						<Form.Group controlId='brand' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='text' placeholder='' value={brand} onChange={(e) => setBrand(e.target.value)}></Form.Control>
						</Form.Group>

						<Form.Group controlId='category' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='text' placeholder='' value={category} onChange={(e) => setCategory(e.target.value)}></Form.Control>
						</Form.Group>

						<Form.Group controlId='countInStock' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='number' placeholder='' value={countInStock} onChange={(e) => setCountInStock(e.target.value)}></Form.Control>
						</Form.Group>

						<Form.Group controlId='description' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='text' placeholder='' value={description} onChange={(e) => setDescription(e.target.value)}></Form.Control>
						</Form.Group>

						<Button type='submit' variant='primary' className='mt-3' disabled={loadingUpdate}>
							
						</Button>
					</Form>
				</FormContainer>
			)}
		</>
	);
};

export default ProductEditPage;
