import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams, useNavigate } from "react-router-dom";
import { Form, Button } from "react-bootstrap";
// Component
import Loader from "../components/Loader";
import Message from "../components/Message";
import FormContainer from "../components/FormContainer";

import { getUserDetails, updateUser } from "../actions/userActions"; // 
import { USER_UPDATE_RESET } from "../constants/userConstants";

const UserEditPage = () => {
	const navigate = useNavigate();
	const { userID } = useParams(); //  App.js  :userID
	// console.log("useParams:", useParams());
	// console.log("userID:", userID);
	const [first_name, setFirst_name] = useState("");
	const [email, setEmail] = useState("");
	const [isAdmin, setIsAdmin] = useState(false); // 

	const dispatch = useDispatch(); //  action  hook

	const userDetails = useSelector((state) => state.userDetails); // 
	// console.log("Redux state userDetails:", userDetails);
	const { loading, error, user } = userDetails;

	const userUpdate = useSelector((state) => state.userUpdate); // 
	const { error: errorUpdate, loading: loadingUpdate, success: successUpdate } = userUpdate;

	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(updateUser({ id: userID, first_name, email, isAdmin }));
	};

	useEffect(() => {
		if (successUpdate) { // 
			dispatch({ type: USER_UPDATE_RESET });
			navigate("/admin/userlist");
		} else { // 
			if (!user.first_name || user.id !== Number(userID)) {
				dispatch(getUserDetails(userID)); // idid
			} else {
				// 
				setFirst_name(user.first_name);
				setEmail(user.email);
				setIsAdmin(user.isAdmin);
			}
		}
	}, [dispatch, user, userID, successUpdate, navigate]);

	return (
		<>
			<Link to='/admin/userlist' className='btn btn-light my-3'>
				
			</Link>

			<h1></h1>
			{loadingUpdate && <Loader />}
			{errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}

			{loading ? (
				<Loader />
			) : error ? (
				<Message variant='danger'>{error}</Message>
			) : (
				<FormContainer>
					<Form onSubmit={submitHandler}>
						<Form.Group controlId='first_name' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='name' placeholder='ID' value={first_name} onChange={(e) => setFirst_name(e.target.value)}></Form.Control>
						</Form.Group>

						<Form.Group controlId='email' className='mb-3'>
							<Form.Label></Form.Label>
							<Form.Control required type='email' placeholder='' value={email} onChange={(e) => setEmail(e.target.value)} autoComplete='username'></Form.Control>
						</Form.Group>

						<Form.Group controlId='isAdmin'>
							<Form.Check type='checkbox' label='' checked={isAdmin} onChange={(e) => setIsAdmin(e.target.checked)}></Form.Check>
						</Form.Group>

						<p>{isAdmin ? "setAdmin()" : "setAdmin()"}</p>

						<Button type='submit' variant='primary' className='mt-3'>
							
						</Button>
					</Form>
				</FormContainer>
			)}
		</>
	);
};

export default UserEditPage;
