import React, { useEffect } from "react";
import { Button, Row, Col, ListGroup, Image, Card ,Table} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";

import { removeFromCart } from "../actions/cartActions";
import { createOrder } from "../actions/orderActions";
// Components
import CheckoutSteps from "../components/CheckoutSteps";
import  Message from '../components/Message';

// import { ORDER_CREATE_RESET } from "../constants/orderConstants";

const PlaceOrderPage = () => {
  
  const orderCreate = useSelector((state) => state.orderCreate);
  const { order, success, error } = orderCreate;

  const navigate = useNavigate();
  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);
  // 
  cart.itemsPrice = cart.cartItems.reduce((acc,currentItem)=>acc+currentItem.price*currentItem.qty,0) // reduce()  callback functionacc  item 
  cart.shippingPrice = cart.itemsPrice > 1000 ? 0 : 50 //  1000 50
  // cart.taxPrice = Number((0.15 * cart.itemsPrice).toFixed(2)) //  15%
  // cart.totalPrice = Number(cart.itemsPrice + cart.shippingPrice) //  + 
  cart.totalPrice = (Number(cart.itemsPrice) + Number(cart.shippingPrice))
  
  const placeOrderHandler = () => {
    dispatch(createOrder({ // 
      orderItems: cart.cartItems,
      shippingAddress: cart.shippingAddress,
      paymentMethod: cart.paymentMethod,
      itemsPrice: cart.itemsPrice,
      shippingPrice: cart.shippingPrice,
      totalPrice: cart.totalPrice,
    }))
    // navigate('/placeorder')
  }

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id));
  }

  if (!cart.paymentMethod) {
    navigate('/payment')
  }

  useEffect(()=>{
    if(success){
      navigate(`/order/${order.id}`)
      dispatch({type:'ORDER_CREATE_RESET'}) // 
    }
  },[success, navigate, order, dispatch])

  return (
    <div>
        <CheckoutSteps loginStep shippingStep paymentStep placeOrderStep />
        <Row>

          <Col md={8}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h2></h2>
                <p>
                  <strong>:</strong>
                  {cart.shippingAddress.address}, {cart.shippingAddress.city},{" "}
                  {cart.shippingAddress.postalCode},{" "}
                  {cart.shippingAddress.country}
                </p>
              </ListGroup.Item>

              <ListGroup.Item>
                <h2></h2>
                <strong>{cart.paymentMethod}</strong>
              </ListGroup.Item>
              
              <ListGroup.Item>
                {cart.cartItems.length === 0 ? ( 
                  <Message variant="info"> &emsp; <Link to='/'></Link> </Message>
                ) : (
                  <ListGroup variant="flush">
                  <ListGroup.Item>
                    <h2></h2>
                    {cart.cartItems.length === 0 ? (
                      <Message variant="info">
                        &emsp;<Link to="/"></Link>
                      </Message>
                    ) : (
                      <Table responsive striped hover>
                        <thead>
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          {cart.cartItems.map((item, index) => (
                            <tr key={index}>
                              <td>
                                <Image src={item.image} fluid rounded width={50}></Image>    
                              </td>
                              <td>
                              <Link to={`/product/${item.product_id}`}>{item.name}</Link>
                              </td>
                              
                              <td>{item.price}</td>
                              <td>{item.qty}</td>
                              <td>{item.price * item.qty}</td>
                              <td>
                              <Button
                                type='button'
                                variant='light'
                                onClick={()=>removeFromCartHandler(item.product_id)}
                              >
                                <i className='fas fa-trash'></i>
                              </Button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                    )}
                  </ListGroup.Item>
                </ListGroup>
                
                ) }
              </ListGroup.Item>

            </ListGroup>
          </Col>
          <Col md={4}>
            <Card>
              <ListGroup variant="flush">

                <ListGroup.Item>
                  <h2></h2>
                </ListGroup.Item>
                
                <ListGroup.Item>
                  <Row>
                    <Col style={{ fontSize: '1.5rem' }}></Col>
                    <Col style={{ fontSize: '1.5rem' }}>${cart.itemsPrice}</Col>
                  </Row>
                </ListGroup.Item>

                <ListGroup.Item>
                  <Row>
                    <Col style={{ fontSize: '1.5rem' }}></Col>
                    <Col style={{ fontSize: '1.5rem' }}>${cart.shippingPrice}</Col>
                  </Row>
                </ListGroup.Item>

                <ListGroup.Item>
                  <Row>
                    <Col style={{ fontSize: '1.5rem' }}></Col>
                    <Col style={{ fontSize: '1.5rem' }}>${cart.totalPrice}</Col>
                  </Row>
                </ListGroup.Item>
                
                <ListGroup.Item>
                  {error && <Message variant='danger'>{error}</Message>}
                </ListGroup.Item>

                <ListGroup.Item>
                  <Button
                    type='button'
                    className='btn-block'
                    disabled={cart.cartItems === 0}
                    onClick={placeOrderHandler}
                  >
                    
                  </Button>
                </ListGroup.Item>

              </ListGroup>
            </Card>
          </Col>
        </Row>
    </div>
  )
}

export default PlaceOrderPage