import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Form, Button } from "react-bootstrap";

import { saveShippingAddress } from "../actions/cartActions";
// Component
import FormContainer from "../components/FormContainer";
import CheckoutSteps from "../components/CheckoutSteps";

const ShippingPage = () => {
	const cart = useSelector((state) => state.cart);
	const { shippingAddress } = cart; //  store  shippingAddress

	const navigate = useNavigate();
	const dispatch = useDispatch();
	const [address, setAddress] = useState(shippingAddress.address);
	const [city, setCity] = useState(shippingAddress.city);
	const [postalCode, setPostalCode] = useState(shippingAddress.postalCode);
	const [country, setCountry] = useState(shippingAddress.country);

	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(saveShippingAddress({ address, city, postalCode, country }));
		navigate("/payment");
	};

	return (
		<FormContainer>
			<CheckoutSteps loginStep shippingStep />
			<h1></h1>

			<Form onSubmit={submitHandler}>
				<Form.Group controlId='address' className='m-3'>
					<Form.Label></Form.Label>
					<Form.Control
						required
						type='text'
						placeholder=''
						value={address ? address : ""} //  React  render address  undefined
						onChange={(e) => setAddress(e.target.value)}
					></Form.Control>
				</Form.Group>

				<Form.Group controlId='city' className='m-3'>
					<Form.Label></Form.Label>
					<Form.Control required type='city' placeholder='' value={city ? city : ""} onChange={(e) => setCity(e.target.value)} autoComplete='username'></Form.Control>
				</Form.Group>

				<Form.Group controlId='postalCode' className='m-3'>
					<Form.Label></Form.Label>
					<Form.Control required type='text' placeholder='' value={postalCode ? postalCode : ""} onChange={(e) => setPostalCode(e.target.value)} autoComplete='current-password'></Form.Control>
				</Form.Group>

				<Form.Group controlId='country' className='m-3'>
					<Form.Label></Form.Label>
					<Form.Control required type='text' placeholder='' value={country ? country : ""} onChange={(e) => setCountry(e.target.value)} autoComplete='new-password'></Form.Control>
				</Form.Group>

				<Button type='submit' variant='primary' className='m-3'>
					
				</Button>
			</Form>
		</FormContainer>
	);
};

export default ShippingPage;
